<template>
	<div class="shezhi">
		<!--设置返回-->
		<div class="shezhi-back">
			<div class="left"  @click="backGo"><img src="/img/wode/shezhi/shezhi-back.png" alt="" /></div>
			<div class="right">设置</div>
		</div>
		<!--设置功能-->
		<div class="gongneng">
			<ul>
				<li v-for="(item,index) in shezhiData" :key="index">
					<div v-if="index == 3" class="item">
						<div class="item-left">{{ item.name }}</div>
						<div class="item-right">{{ item.name2 }}</div>
					</div>
					<div v-else-if="index == 5" class="item">
						<div class="item-left left">
							<div>{{ item.name }}</div>
							<div>{{ item.name2 }}</div>
						</div>
						<div class="item-right" @click="qkhc">
							<img :src="item.img" alt="" />
						</div>
					</div>
					<div v-else class="item">
            <div class="item-left">{{ item.name }}</div>
            <div class="item-right">
              <div v-if="index == 0" @click="xggrxx"><img :src="item.img" alt="" /></div>
              <div v-else-if="index == 1" @click="zhyaq"><img :src="item.img" alt="" /></div>
              <div v-else-if="index == 2" @click="pssm"><img :src="item.img" alt="" /></div>
              <div v-else @click="gywm"><img :src="item.img" alt="" /></div>
            </div>
					</div>
				</li>
			</ul>
		</div>
		<!--退出登录-->
		<div class="login-end" @click="tcdl" v-show="logined">
			<div class="logout">退出登录</div>
		</div>
    <!-- 点击登录 -->
    <div class="login-end" @click="tcdl2" v-show="!logined">
    	<div class="logout">点击登录</div>
    </div>
		<!-- 弹出层-->
		<div class="tanchuceng" v-show="show"></div>
		<div class="tanchu-logo" v-show="show"><img src="/img/shezhi-logo.png" alt="" /></div>
		<div class="tanchu" v-show="show">
			<div class="tanchu-top">
				<div class="tanchu-title">温馨提示</div>
				<div class="tanchu-content">您确定要退出登录吗？</div>
			</div>
			<div class="tanchu-bottom">
				<div @click="tanChuFalse" class="tanchu-left">我再想想</div>
				<div @click="tanChuEnd" class="tanchu-right">狠心离开</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "shezhi",
		data: function() {
			return {
        logined: false,
				shezhiData: [
					{
						name: "修改个人信息",
						img: "/img/wode/jichu/wode-back.png"
					},
					{
						name: "账户与安全",
						img: "/img/wode/jichu/wode-back.png"
					},
					{
						name: "配送说明",
						img: "/img/wode/jichu/wode-back.png"
					},
					{
						name: "版本更新",
						name2: "版本号3.0"
					},
					{
						name: "关于我们",
						img: "/img/wode/jichu/wode-back.png"
					},
					{
						name: "清空缓存",
						name2: "334.9MB",
						img: "/img/wode/jichu/wode-back.png"
					}
				],
				show: false
			}
		},
    created() {
      if(this.$store.state.user.username != null){
        this.logined = true
      }
    },
		methods: {
			// 修改个人信息
			xggrxx(){
				console.log('修改个人信息')
			},
			// 账户与安全
			zhyaq(){
				console.log('账户与安全')
				this.$router.push('/userSafe')
			},
			// 配送说明
			pssm(){
				console.log('配送说明')
          this.$router.push("/peisongshuoming")
			},
			// 关于我们
			gywm(){
				console.log('关于我们')
          this.$router.push("/guanyuwomen")
			},
			// 清空缓存
			qkhc(){
				console.log('清空缓存')
			},
			backGo(){
				this.$router.go(-1)
			},
			// 弹出层 我再想想
			tanChuFalse(){
				this.show = false
				var mo = function(e) {
					e.preventDefault()
				}
				document.body.style.overflow = ''
				document.addEventListener('touchmove', mo, false)
			},
			// 弹出层 狠心离开
			tanChuEnd(){
				this.show = false
				var mo = function(e) {
					e.preventDefault()
				}
				document.body.style.overflow = ''
				document.addEventListener('touchmove', mo, false)
				this.$store.commit('clearUser')
				this.$router.push('/login')
			},
			// 注销登录
			tcdl(){
				console.log('注销登录')
				this.show = true
				var mo = function(e) {
					e.preventDefault()
				}
				document.body.style.overflow = 'hidden'
				document.addEventListener('touchmove', mo, false)
			},
      // 点击登录
      tcdl2(){
        this.$router.push('/login')
      }
		}
	}
</script>

<style scoped>
	.shezhi{
		width:100%;
		height:100%;
		padding:0 30px;
		position:relative;
	}
	/*设置返回*/
	.shezhi .shezhi-back{
		display: flex;
		justify-content: space-between;
		font-size:22px;
		line-height:90px;
		text-align: center;
	}
	.shezhi .shezhi-back .right{
		width:100%;
		font-size: 36px;
    color: #1b1b1b;
	}
	/*设置功能*/
  .shezhi .gongneng ul{
    font-size: 26px;
    color: #1b1b1b;
  }
  .shezhi .gongneng ul li{
    border-bottom: 1px solid #eeeeee;
  }
  .shezhi .gongneng ul li:first-child{
    border-top: 1px solid #eeeeee;
  }


  .shezhi .gongneng ul li .item{
		width:100%;
		height:80px;
		line-height:80px;
		display: flex;
		justify-content: space-between;
	}
	.shezhi .gongneng ul li .item .left{
		width:95%;
		display: flex;
		justify-content: space-between;
	}
	.shezhi .login-end{
		width:100%;
		height:100%;
	}
	.shezhi .login-end .logout{
		width:60%;
		font-size:26px;
    color: white;
		line-height:60px;
		border-radius: 10px;
		text-align: center;
		background-color:#84c225;
		margin:0 auto;
    margin-top: 100px;
	}
	/*  弹出层*/

	.shezhi .tanchuceng {
		width: 100%;
		height: 1034px;
		background-color: #c0c0c0;
		position: absolute;
		left: 0;
		top: 0;
		opacity: 0.4;
	}
	.tanchu-logo{
		position:absolute;
		left:314px;
		top:185px;
		/*z-index:0px;*/
	}
	.tanchu {
		width: 500px;
		height: 300px;
		background-color: white;
		position: fixed;
		left: 125px;
		top: 275px;
	}
	.tanchu .tanchu-top{
		padding: 0 30px;
	}
	.tanchu .tanchu-title{
		width:100%;
		font-size:20px;
		color:#84c225;
		padding:40px 0;
		text-align: center;
		border-bottom: 1px solid #84c225;
	}
	.tanchu .tanchu-content{
		width:100%;
		font-size:20px;
		color:#333333;
		padding:40px 0 40px 20px;
	}
	.tanchu .tanchu-bottom{
		width:100%;
		height:80px;
		display: flex;
		justify-content: space-between;
		border-top:1px solid #f4f4f4;
	}
	.tanchu .tanchu-bottom div{
		width:50%;
		height:100%;
		font-size:20px;
		line-height:80px;
		text-align: center;
	}
	.tanchu .tanchu-bottom .tanchu-left{
		color:#84c225;
	}
	.tanchu .tanchu-bottom .tanchu-right{
		color:#a9a6a9;
	}
</style>
